.home {
    display: flex;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    text-align: center;
}

.home img {
    max-width: 100%;
    border-radius: 0.5rem;
    box-shadow: 0 4px 16px rgba(14, 33, 72, 0.3);
}